body {
    font-family: 'Gilroy', sans-serif;
    font-weight: 400;
    height: 100%;
    overflow-x: hidden;
    color: #23272B;
    letter-spacing: 0.02em;
    &.fixed-body {
        overflow-y: hidden;
    }
}

.container {
    margin: 0 auto;
    max-width: 1440px;
    padding: 0 15px;
    @media screen and (max-width: 1200px) {
        max-width: 970px;
    }
}

.btn {
    display: flex;
    align-items: center;
    justify-content: center;
}

.display1 {
    font-size: 45px;
    line-height: 120%;
    @media screen and (max-width: 1200px) {
        font-size: 38px;
    }
    @media screen and (max-width: 992px) {
        font-size: 32px;
    }
    @media screen and (max-width: 768px) {
        font-size: 25px;
    }
}

.tab-slider {
    display: flex;
    align-items: center;
    &__item {
        width: unset;
        display: flex;
        align-items: center;
        height: 44px;
        background: #F9F9F9;
        border-radius: 60px;
        padding: 0 25px;
        font-size: 14px;
        flex-shrink: 0;
        white-space: nowrap;
        svg {
            margin-right: 14px;
        }
        &:hover {
            background: #2D9586;
            cursor: pointer;
            color: #fff;
            path {
                fill: #fff;
            }
        }
        &--active {
            background: #2D9586;
            pointer-events: none;
            color: #fff;
            path {
                fill: #fff;
            }
        }
    }
    &__prev,
    &__next {
        position: static;
        margin: 0;
    }
    .swiper-container {
        overflow: hidden;
        margin: 0 20px;
    }
    &__content {
        display: none;
        &--active {
            display: block;
        }
    }
}